body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background: #e3f6f5 !important;
}
.app-div > * {
  margin: 10px 0;
}

* {
  text-decoration: none;
  font-family: Mulish;
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  list-style: none;
}

body {
  display: flex;
  align-items: center;
  justify-content: center;
}

.todo-app {
  width: 897px;
  background-color: #2e2e4b;
  padding: 40px 80px;
  color: white;
}

.todo-head {
  display: flex;
  justify-content: space-between;
}

.todo-head .left-side {
  width: 75%;
  word-break: break-all;
}

.todo-head .left-side p {
  width: 75%;
}

.todo-head .head-buttons {
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}

.todo-head .head-buttons .first-child {
  color: #7effa5;
  background-color: rgba(126, 255, 165, 0.05);
  margin-bottom: 10px;
  border-radius: 15px;
  padding: 6px 15px;
}

.todo-head .head-buttons .second-child {
  color: #ff7e9c;
  background-color: rgba(255, 126, 156, 0.05);
  border-radius: 15px;
  padding: 6px 15px;
}

.todo-add {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 30px;
  background-color: #1b1b30;
  border-radius: 10px;
}

.todo-add .reset-div {
  display: flex;
  justify-content: space-between;
  padding-bottom: 10px;
}

.todo-add .reset-div button {
  padding: 8px 15px;
  border-radius: 17.5px;
  font-weight: normal;
  font-size: 14px;
  line-height: 20px;
  color: #ff0000;
  background-color: rgba(255, 0, 0, 0.05);
}

.todo-add .reset-div h5 {
  padding-top: 10px;
}

.input-div {
  background-color: #1b1b30;
  padding-bottom: 0;
}

.input-div input {
  width: 100%;
  padding: 20px 75px;
  background-color: #2a2a42;
  border: 1px solid #454564;
  border-radius: 5px;
  outline: none;
  text-transform: capitalize;
  color: white;
}

.input-div i {
  position: relative;
  bottom: 50px;
  left: 20px;
  font-size: 35px;
}

.buttons {
  display: flex;
  justify-content: space-between;
}

.buttons .button-add {
  background-color: #786cd5;
  width: 247px;
  height: 56px;
  border-radius: 5px;
  color: #fff;
}

.buttons .button-reset {
  background-color: #501a1a;
  color: #fff;
  border-radius: 5px;
  width: 247px;
  height: 56px;
}
/*# sourceMappingURL=Todo.css.map */
